export default {
	props:{
		item:Object,
	},
	methods:{
		//跳转类型
		clickeven(){
			switch (this.item.clicktype){
				case "navigateTo":
				if(this.item.url){
					uni.navigateTo({url: this.item.url});
				}
				break;
				case "switchTab":
				if(this.item.url){
					uni.switchTab({url: this.item.url})
				}
				break;
				case "clear":
				uni.showModal({
					title:"提示",
					content:"是否要清除缓存",
					confirmText:"立刻清除",
					success:res=>{
						uni.clearStorage();//清除缓存
					    uni.showToast({
					    	title:"清除缓存成功"
					    })
					}
				})
			}
		}
	}
}
